import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { catchError, take } from 'rxjs/operators';
import { RolesDatabase } from './roles.database';
import { RoleDto } from '@shared/service-proxies/service-proxies';

@Injectable({
  providedIn: 'root'
})
export class RolesResolver implements Resolve<RoleDto> {
  constructor(private router: Router, private database: RolesDatabase) {}

  resolve(route: ActivatedRouteSnapshot): Observable<RoleDto> {
    const id = Number(route.paramMap.get('id'));

    return this.database.get(id).pipe(
      take(1),
      catchError(() => {
        this.router.navigate(['/app/roles']);
        return of({} as RoleDto);
      })
    );
  }
}
