import { SelectionModel } from '@angular/cdk/collections';
import { Component, OnInit, Input, Injector } from '@angular/core';
import { RolesDatasource } from '@app/roles/services';
import { AppComponentBase } from '@shared/app-component-base';
import { RoleDto } from '@shared/service-proxies/service-proxies';

@Component({
  selector: 'app-roles-list-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss']
})
export class RolesListTableComponent extends AppComponentBase implements OnInit {
  @Input() source: RolesDatasource;
  @Input() columns = ['select', 'name', 'actions'];

  selection: SelectionModel<number>;

  constructor(injector: Injector) {
    super(injector);
  }

  ngOnInit() {
    const initialSelection = [];
    const allowMultiSelect = true;
    this.selection = new SelectionModel<number>(allowMultiSelect, initialSelection);
  }

  editUser(user: RoleDto) {
    // TODO update user
  }

  batchDelete() {
    // TODO use the selection.selected
    // FIXME prevent admin role to be deleted
  }

  /**
   * Selection Support
   */
  isAllSelected() {
    return !this.source.data.some(row => !this.selection.isSelected(row.id));
  }

  masterToggle() {
    const deselect = this.isAllSelected();
    this.source.data.forEach(row => {
      deselect ? this.selection.deselect(row.id) : this.selection.select(row.id);
    });
  }
}
