import { Injectable } from '@angular/core';
import { DataSourceItem, ReactiveDataSource } from '@matheo/datasource';
import { of } from 'rxjs';
import { RolesDatabase } from './roles.database';
import { RolesGetAllRequest } from './roles.interfaces';
import { PagedResultDtoOfRoleDto, RoleDto } from '@shared/service-proxies/service-proxies';

@Injectable()
export class RolesDatasource extends ReactiveDataSource<
  RolesGetAllRequest,
  PagedResultDtoOfRoleDto,
  RoleDto
> {
  constructor(protected database: RolesDatabase) {
    super();

    this.config = {
      debug: false,
      autoStart: true // filter BehaviorSubject
    };
    this.pageSize = 10;
  }

  rawDefault() {
    return {
      totalCount: 0,
      items: []
    } as PagedResultDtoOfRoleDto;
  }

  rawFetch(args: RolesGetAllRequest) {
    return this.database.getAll(args);
  }

  rawTotal(result: PagedResultDtoOfRoleDto) {
    return of(result.totalCount);
  }

  rawResult(result: PagedResultDtoOfRoleDto): RoleDto[] {
    return result.items;
  }

  filter(query: string, limit: number): void {
    throw new Error('Not Implemented');
  }

  resFilter(result: RoleDto[]): DataSourceItem[] {
    throw new Error('Not Implemented');
  }
}
