import { Injectable } from '@angular/core';
import { RoleServiceProxy } from '@shared/service-proxies/service-proxies';
import { RolesGetAllRequest } from './roles.interfaces';

@Injectable({
  providedIn: 'root'
})
export class RolesDatabase {
  constructor(private roles: RoleServiceProxy) {}

  get(id?: number | null) {
    return this.roles.get(id);
  }

  getAll(args: RolesGetAllRequest) {
    return this.roles.getAll(args.query, args.pageIndex, args.pageSize);
  }
}
