import { Component, OnInit, Injector } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AppComponentBase } from '@shared/app-component-base';
import { AppConsts } from '@shared/AppConsts';
import { RoleDto } from '@shared/service-proxies/service-proxies';
import { FormUtils } from '@shared/utils';
import { PermissionsDatabase } from '../services';

@Component({
  selector: 'app-roles-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss']
})
export class RolesFormComponent extends AppComponentBase implements OnInit {
  form: FormGroup;
  item: RoleDto;

  isSaving = false;

  constructor(
    injector: Injector,
    private route: ActivatedRoute,
    private router: Router,
    private builder: FormBuilder,
    private database: PermissionsDatabase
  ) {
    super(injector);
  }

  ngOnInit() {
    this.form = this.builder.group({
      id: null,
      name: [null, Validators.required],
      displayName: [null, Validators.required],
      description: null,
      permissions: [AppConsts.mandatoryPerms, Validators.required]
    });

    // route parameters
    this.route.data.subscribe((data: any) => {
      this.item = data.item;
      if (this.item) {
        this.form.patchValue(this.item);
      }
    });
  }

  onSubmit() {
    if (this.form.valid) {
      this.isSaving = true;

      this.database
        .save(FormUtils.pruneEmpty(this.form.value))
        .subscribe(() => {
          this.router.navigate(['/app/roles'])
        },
        err => {
          console.error(err);
          this.isSaving = false;
        });

    } else {
      FormUtils.markAsTouched(this.form);
      this.isSaving = false;
    }
  }
}
