import { SelectionModel } from '@angular/cdk/collections';
import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  forwardRef,
  Host,
  Injector,
  Input,
  Optional,
  OnInit,
  Output,
  SkipSelf,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import {
  NG_VALUE_ACCESSOR,
  NG_VALIDATORS,
  AbstractControl,
  ControlContainer,
  ControlValueAccessor,
  FormBuilder,
  FormControl,
  FormGroup
} from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { getterPaginator } from '@matheo/datasource';
import { AppConsts } from '@shared/AppConsts';
import { AppComponentBase } from '@shared/app-component-base';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { PermissionsDatasource } from '../services';

@Component({
  selector: 'app-roles-permissions',
  templateUrl: './permissions.component.html',
  styleUrls: ['./permissions.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    PermissionsDatasource,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RolesPermissionsComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => RolesPermissionsComponent),
      multi: true
    }
  ]
})
export class RolesPermissionsComponent extends AppComponentBase implements OnInit, ControlValueAccessor {
  form: FormGroup;

  @Input() formControlName: string;
  @Input() columns = ['select', 'name'];

  @Output() change = new EventEmitter<string[]>();

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  value = [];
  selection: SelectionModel<string>;
  mandatory = AppConsts.mandatoryPerms;

  _required: boolean;
  _disabled = false;

  constructor(
    @Optional()
    @Host()
    @SkipSelf()
    private parent: ControlContainer,
    private builder: FormBuilder,
    public source: PermissionsDatasource,
    injector: Injector,
  ) {
    super(injector);
  }

  ngOnInit() {
    this.form = this.builder.group({
      query: null
    });

    if (this.formControlName) {
      const parent = this.parent.control as AbstractControl;
      const control = parent.get(this.formControlName);
      this._required = this.checkRequired(control);
    }

    // datasource setup
    this.source.setPaginator(this.paginator, getterPaginator(this.paginator));

    this.source.addOptional({
      stream: this.form.valueChanges.pipe(debounceTime(500), distinctUntilChanged()),
      getter: () => this.form.value
    });

    // selection setup
    this.selection = new SelectionModel<string>(true, []);

    this.selection.changed.subscribe(
      () => this.onChange(this.selection.selected)
    );
  }

  propagateChange = (data: any) => {};
  propagateTouch = () => {};

  onChange(value: string[]) {
    this.value = value;
    this.propagateChange(this.value);
    this.change.emit(this.value);
  }

  /**
   * Control Value Accessor Methods
   */
  writeValue(value: string[]): void {
    if (value && value.length) {
      this.value = value;
      this.selection.select(...value);
    }
  }

  registerOnChange(fn: any): void {
    this.propagateChange = (data: any) => {
      fn(data);
    };
  }

  registerOnTouched(fn: any): void {
    this.propagateTouch = () => {
      fn();
    };
  }

  setDisabledState(isDisabled: boolean): void {
    this._disabled = isDisabled;
  }

  validate(c: FormControl) {
    if (!this._disabled) {
      if (this._required && !this.value.length) {
        return { required: true };
      }
    }
    return null;
  }

  checkRequired(field: AbstractControl) {
    if (field.validator) {
      const validator = field.validator(field);
      if (validator && validator.required) {
        return true;
      }
    }
    return false;
  }

  /**
   * Selection Support
   */
  isAllSelected() {
    return !this.source.data.some(row => !this.selection.isSelected(row.name));
  }

  masterToggle() {
    const deselect = this.isAllSelected();
    this.source.data.forEach(row => {
      return deselect
        ? this.mandatory.indexOf(row.name) === -1 ? this.selection.deselect(row.name) : null
        : this.selection.select(row.name);
    });
  }
}
