import { Injectable } from '@angular/core';
import { PermissionDto, RoleDto, RoleServiceProxy } from '@shared/service-proxies/service-proxies';
import { of } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { ListRequest } from './permissions.interfaces';

@Injectable({
  providedIn: 'root'
})
export class PermissionsDatabase {
  perms: PermissionDto[] = [];

  constructor(private roles: RoleServiceProxy) {}

  getAll(args: ListRequest) {
    const offset = args.pageIndex * args.pageSize;
    const limit = args.pageSize;

    const response = () => {
      // filter the list if there's a query
      const perms = args.query
        ? this.perms.filter(perm => {
            const regex = new RegExp(args.query, 'i');
            return regex.test(perm.name) || regex.test(perm.displayName);
          })
        : this.perms;

      return {
        totalCount: perms.length,
        items: perms.slice(offset, offset + limit)
      };
    };

    if (this.perms.length) {
      return of(response());
    } else {
      return this.roles.getAllPermissions().pipe(
        tap(perms => (this.perms = perms.items)),
        map(() => response())
      );
    }
  }

  save(item: RoleDto) {
    return item.id
    ? this.roles.update(item)
    : this.roles.create(item);
  }
}
