import { Injectable } from '@angular/core';
import { DataSourceItem, ReactiveDataSource } from '@matheo/datasource';
import { PermissionDto } from '@shared/service-proxies/service-proxies';
import { of } from 'rxjs';
import { PermissionsDatabase } from './permissions.database';
import { ListRequest, PagedResultDtoOfPermissionDto } from './permissions.interfaces';

@Injectable()
export class PermissionsDatasource extends ReactiveDataSource<
  ListRequest,
  PagedResultDtoOfPermissionDto,
  PermissionDto
> {
  constructor(protected database: PermissionsDatabase) {
    super();

    this.config = {
      debug: false,
      autoStart: true
    };
    this.pageSize = 7;
  }

  rawDefault() {
    return {
      totalCount: 0,
      items: []
    } as PagedResultDtoOfPermissionDto;
  }

  rawFetch(args: ListRequest) {
    return this.database.getAll(args);
  }

  rawTotal(result: PagedResultDtoOfPermissionDto) {
    return of(result.totalCount);
  }

  rawResult(result: PagedResultDtoOfPermissionDto): PermissionDto[] {
    return result.items;
  }

  filter(query: string, limit: number): void {
    throw new Error('Not Implemented');
  }

  resFilter(result: PermissionDto[]): DataSourceItem[] {
    throw new Error('Not Implemented');
  }
}
